<template>
  <div class="students padding">
    <!-- 学生管理 -->
    <div>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/main/students' }">基础信息</el-breadcrumb-item>
        <el-breadcrumb-item>
          学生管理
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="examination-dv">
        <img src="@/assets/mao.png" alt="">
        <span>学生管理</span>
    </div>
    <div class="students-inp">
      <el-form :inline="true" :model="formInline" class="demo-form-inline">
        <span class="ssssss">所属学院：</span>
        <el-select v-model="value0" placeholder="请选择学院" class="pppp" @change="changes1">
          <el-option
            v-for="item in options0"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <span class="sssssss">所属专业：</span>
        <el-select v-model="valuearr1" placeholder="请先选择学院" class="pppp" @change="changes3">
          <el-option
            v-for="item in optionsarr1"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        <span class="sssssss">所属年级：</span>
        <el-select v-model="valuearr2" placeholder="全部"  class="pppp" >
          <el-option
            v-for="item in optionsarr2"
            :key="item.index"
            :label="`${item.grade}级`"
            :value="item.grade"
          ></el-option>
        </el-select>
        <span class="sssssss">所属班级：</span>
        <el-select v-model="valuearr3" placeholder="请先选择专业"  class="pppp"  >
          <el-option
            v-for="item in optionsarr3"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select><br><br><br>
        <span class="sssssssl">在校状态：</span>
        <el-select v-model="valuearr5" placeholder="全部"  class="pppp">
          <el-option
            v-for="item in optionsarr5"
            :key="item.code"
            :label="item.value"
            :value="item.code"
          ></el-option>
        </el-select>
        <el-form-item label="搜索:">
          <el-input v-model="formInline.code" placeholder="学号，姓名" ></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit" class="btn">搜索</el-button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <!-- <el-button type="success" round> <i class="el-icon-document"></i>&nbsp;&nbsp;发起问卷</el-button>&nbsp;&nbsp; -->
            <!-- <el-button type="primary" round @click="close">+ 新建</el-button> -->
        </el-form-item>
      </el-form>
       <div class="students-btn">
            <el-button type="primary" round @click="close">+ 新建</el-button>
        </div>
    </div>

       <!-- 弹框组件 -->
    <el-dialog :title="addst" :visible.sync="dialogFormVisible" :before-close="handleClose" :close-on-click-modal='false'>
      <el-form :model="form" :rules="rules" ref="ruleForm">
         <el-form-item label="所属学院：" :label-width="formLabelWidth" >
         <el-select v-model="value" placeholder="" @change="changes2">
          <el-option
            v-for="item in options"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        </el-form-item>
        <!-- <span class="ssss">所属学院：</span> -->
        <el-form-item label="所属专业：" :label-width="formLabelWidth" >
          <el-select v-model="value1" placeholder="" @change="changes33">
          <el-option
            v-for="item in options1"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
        </el-form-item>
        <!-- <span class="ssss">所属专业：</span> -->
         <el-form-item label="所属年级：" :label-width="formLabelWidth" >
            <el-select v-model="value2" placeholder="" >
            <el-option
              v-for="item in options2"
              :key="item.id"
              :label="`${item.grade}级`"
              :value="item.grade"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <span class="ssss">所属年级：</span> -->
         <el-form-item label="所属班级：" :label-width="formLabelWidth" >
          <el-select v-model="value3" placeholder="">
            <el-option
              v-for="item in options3"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <span class="ssss">所属班级：</span> -->
        
        <el-form-item label="入学年级：" :label-width="formLabelWidth" prop="grade">
          <el-input v-model="form.grade" auto-complete="off" ></el-input>
        </el-form-item>
        <!-- <span class="ssss">培养方案：</span>
        <el-select v-model="value6" placeholder="">
          <el-option
            v-for="item in options6"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select> -->
        <el-form-item label="学号：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="姓名：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off"></el-input> 
        </el-form-item>
        <el-form-item label="性别：" :label-width="formLabelWidth" >
            <el-select v-model="value4" placeholder="">
            <el-option
              v-for="item in options4"
              :key="item.id"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <span class="sssss">性别：</span> -->
        <el-form-item label="民族：" :label-width="formLabelWidth"  >
          <el-select v-model="value7" placeholder="">
          <el-option
            v-for="item in options7"
            :key="item.id"
            :label="item.value"
            :value="item.code"
          ></el-option>
        </el-select>
        </el-form-item>
         <!-- <span class="sssss">民族：</span> -->
        
        <el-form-item label="手机号：" :label-width="formLabelWidth" prop="phone" >
          <el-input v-model="form.phone" auto-complete="off"></el-input> 
        </el-form-item>
        <el-form-item label="邮箱：" :label-width="formLabelWidth" prop="email" >
          <el-input v-model="form.email" auto-complete="off"></el-input> 
        </el-form-item>
        <el-form-item label="身份证号：" :label-width="formLabelWidth" prop="idNumber" >
          <el-input v-model="form.idNumber" auto-complete="off" ></el-input>
        </el-form-item>
         <el-form-item label="出生年月：" :label-width="formLabelWidth" prop="birthday" >
          <el-input v-model="form.birthday" auto-complete="off" placeholder="2019-05-17"></el-input> 
        </el-form-item>
         <el-form-item label="地址：" :label-width="formLabelWidth"  >
          <el-cascader
            v-model="selectedOptions"
            placeholder="请选择省市区"
            :options="cascaderData"
            @active-item-change="handleItemChange"
            :change-on-select="false"
            :props="props"
        ></el-cascader>
        </el-form-item>
         <!-- <span class="ssss1">地址：</span> -->
         
        <el-form-item label="在校状态：" :label-width="formLabelWidth" >
          <el-select v-model="value5" placeholder="">
          <el-option
            v-for="item in options5"
            :key="item.code"
            :label="item.value"
            :value="item.code"
          ></el-option>
        </el-select>
        </el-form-item>
        <!-- <span class="ssss">在校状态：</span> -->
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('ruleForm')">取 消</el-button>
        <el-button type="primary" @click="seccons4('ruleForm')" v-show="falg">确 定</el-button>
        <el-button type="primary" @click="seccons44" v-show="falg1">确 定</el-button>
      </div>
    </el-dialog>

    <div class="students-tab">
      <template>
        <el-table :data="tableData" border stripe  style="width: 100%" v-loading="loading">
          <!-- <el-table-column
              type="selection"
              width="55">
          </el-table-column> -->
          <el-table-column label="学号"  width="140" >
            <template slot-scope="scope">
              <span >{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="姓名" >
            <template slot-scope="scope">
              <span >{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="入学年级" >
            <template slot-scope="scope">
              <span >{{ scope.row.grade }}级</span>
            </template>
          </el-table-column>
          <el-table-column label="所属院校" >
            <template slot-scope="scope">
              <span >{{ scope.row.academyVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属专业" >
            <template slot-scope="scope">
              <span
                style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;"
              >{{ scope.row.specialtyVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属班级" >
            <template slot-scope="scope">
              <span >{{ scope.row.classVal }}</span>
            </template>
          </el-table-column>
          <!-- <el-table-column label="试用陪养方案" >
            <template slot-scope="scope">
              <span >{{ scope.row.versionVal }}</span>
            </template>
          </el-table-column> -->
          <el-table-column label="在校状态" >
            <template slot-scope="scope">
              <span >{{ scope.row.atSchoolStatusVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)"><i class="el-icon-edit"></i>&nbsp;&nbsp;编辑</el-button>
              <el-button type="text" size="small"  @click="handleDelete(scope.$index, scope.row)"><i class="el-icon-delete"></i>&nbsp;&nbsp;删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
           background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import { setTimeout } from 'timers';
export default {
  data() {
    //  身份证信息检验
     let validID=(rule,value,callback)=>{
      if(value==''||value==undefined){
          callback()
      }else{
          let reg=/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/
          console.log(reg.test(value),'reg.test(value)')
          if(!reg.test(value)){
            callback(new Error('请输入正确的身份证格式'))
          }else{
              callback()
          }
      }
    };
    //   电话号码检验
    let validMobile=(rule,value,callback)=>{
      if(value==''||value==undefined){
          callback()
      }else{
          let reg=/[0-9]{11}/
          if(!reg.test(value)){callback(new Error('请输入正确的手机号格式'))}else{
              callback()
          }
      }
    };
    //时间格式化验证
    let checkDate=(rule,value,callback)=>{
      if(value==''||value==undefined){
          callback()
      }else{
          let reg= /^(\d{1,4})(-|\-)(\d{1,2})\2(\d{1,2})$/;
          if(!reg.test(value)){callback(new Error('请按照提示输入正确格式'))}else{
              callback()
          }
      }
    };
    return {
      dialogFormVisible: false,
      formLabelWidth: '120px',
      departmentOptions: [],
      cascaderData: [],
      province:'',
      city:'',
      area:'',
      falg:false,
      falg1:false,
       props: {
          value: 'code',
          label:'name',
          children: 'cities'
        },
      selectedOptions: [],
       form: {
          name: "",
          code: "",
          gender: 0,
          email: "",
          phone: "",
          grade: 0,
          classId: "",
          specialtyId: "",
          academyId: "",
          versionId: "",
          ethnicity: 0,
          idNumber: "",
          birthday: "",
          province: "",
          city: "",
          area: "",
          zaixiao:'',
          xueyuan:'',
          zhuanye:''
        },
      pageNum: 1,
      total: 0,
      page: 1,
      pageSize: 15,
      formInline: {
        code: "",
        region: ""
      },
      rules:{
        name: [{required: true, message: '请输入学生姓名', trigger: 'blur'}],
        code: [{required: true, message: '请输入学生学号', trigger: 'blur'}],
        grade: [{required: true, message: '请输入入学年份', trigger: 'blur'}],
        zhuanye: [{required: true, message: '请选择专业', trigger: 'blur'}],
        phone:[{validator:validMobile,trigger:'blur'}],
        email:[{type:'email',message:'邮箱格式不对',trigger:'blur'}],
        birthday:[{validator:checkDate,trigger:'blur'}],
        idNumber:[{validator:validID,trigger:'blur'}],
      },
      options:[],
      value: '',
      options0:[],
      value0: '',
      options1:[],
      value1: '',
      optionsarr1:[],
      valuearr1: '',
      options2:[],
      value2: '',
      options3:[],
      value3: '',
      optionsarr2:[],
      valuearr2: '',
      optionsarr3:[],
      valuearr3: '',
      options4:[{"code": 0,"value": "男"},{"code": 1,"value": "女"},],
      value4: 0,
      options5:[],
      value5: '',
      optionsarr5:[],
      valuearr5: '',
      options6:[],
      value6: '',
      options7:[],
      value7: '',
      tableData: [],
      loading:true,
      code:0,
      addst:''
    };
   
  },
  methods: {
    getNodes (val) {
      let parentCode = 37
      this.$get(`/system/dist/list/${parentCode}`, ).then(res => {
        this.cascaderData = res.data
        for(var i= 0 ;i<this.cascaderData.length; i++){
          this.cascaderData[i].cities=[]
        } 
      })
    },
    handleItemChange (val) {
      let index = 0
      if(val.length === 1) {
        this.$get(`/system/dist/list/${val[0]}`).then(res => {
          for(var i= 0 ;i<res.data.length; i++){
            res.data[i].cities=[]
          } 
          for(var i= 0 ;i<this.cascaderData.length; i++){
            if (val[0] == this.cascaderData[i].code) {
              this.cascaderData[i].cities = res.data
              this.province =  this.cascaderData[i].code 
              this.cascaderData = JSON.parse(JSON.stringify(this.cascaderData))
              break
            }
          }
        })
      } else if (val.length === 2) {
        this.$get(`/system/dist/list/${val[1]}`).then(res => {
          for(var i= 0 ;i<this.cascaderData.length; i++){
            if (val[0] == this.cascaderData[i].code) {
              for(let j = 0; j < this.cascaderData[i].cities.length; j++) {
                if (val[1] === this.cascaderData[i].cities[j].code) {
                  this.cascaderData[i].cities[j].cities = res.data
                  this.city =  this.cascaderData[i].cities[j].code
                  this.cascaderData = JSON.parse(JSON.stringify(this.cascaderData))
                  this.area = this.cascaderData[i].cities[j].cities[i].code
                  break
                }
              }
              
            }
          }
        })
      }
    },
    onSubmit() {
      if(this.value2 == '全部'){
        this.value2 = null
      }
      let keywords = {
          academyId:this.value0,
          specialtyId:this.valuearr1,
          grade:this.valuearr2,
          classId:this.valuearr3,
          atSchoolStatus:this.valuearr5,
          keywords:this.formInline.code,
          rows : this.pageSize,
          page : this.pageNum
      }
      this.$post('/basic/student/list',keywords).then(res=>{
          if(res.status == 1){
            this.tableData = res.data
            this.total = res.total
            this.loading = false
          }
      })
      
    },
    //培养方案    /basic/version/all
    versionall(){
      this.$get('/basic/version/all').then(res=>{
        this.options6 = res.data
      })
    },
    // /basic/academy/all 所有学院
    allxue(){
      this.$get(`/basic/academy/all?type=${1}`).then(res=>{
        if(res.status== 1){
           this.options0 = res.data
          this.options0.unshift({code:'',name:'全部'})
        }
      })
    },
    // 所有年级 /basic/class/grades
    classgrades(){
      this.$get('/basic/class/grades').then(res=>{
        if(res.status == 1){
           this.options2 =JSON.parse(JSON.stringify(res.data))
          this.optionsarr2= res.data
          this.optionsarr2.unshift({grade:'全部'})
        }
       
      })
    },
    // 学生列表
    studentlist(){
      let keywords = {
          rows : this.pageSize,
          page : this.pageNum
      }
       this.$post('/basic/student/list',keywords).then(res=>{
          if(res.status == 1){
            this.tableData = res.data
            this.total = res.total
            this.loading = false
          }
      })
    },
    allxue1(){
      this.$get(`/basic/academy/all?type=${1}`).then(res=>{
        if(res.status == 1){
             this.options = JSON.parse(JSON.stringify(res.data))
        }
      })
    },
    changes1(ids){
      let id = ids || ''
      this.$get(`/basic/specialty/all/${id}`).then(res=>{
        if(res.status == 1){
           this.optionsarr1 = res.data
          this.optionsarr1.unshift({code:'',name:'全部'})
        }
         
      })
    },
    changes2(ids){
      let id = ids || ''
      this.$get(`/basic/specialty/all/${id}`).then(res=>{
        if(res.status == 1){
          this.options1 = JSON.parse(JSON.stringify(res.data))
        }
          
      })
    },
    changes3(){
       let specialtyID = this.valuearr1
       this.$get(`/basic/class/all/${specialtyID}`).then(res=>{
         if(res.status == 1){
           this.optionsarr3 =res.data
         }
      })
    },
    changes33(){
       let specialtyID = this.value1
       this.$get(`/basic/class/all/${specialtyID}`).then(res=>{
         if(res.status == 1){
            this.options3 = res.data
         }
      })
    },
    // 在校状态   /system/dict/field/atSchoolStatus
    atSchoolStatus(){
      this.$get('/system/dict/field/atSchoolStatus').then(res=>{
        if(res.status == 1){
          this.options5 = JSON.parse(JSON.stringify(res.data))
          this.optionsarr5 = res.data
          this.optionsarr5.unshift({code:null,value:'全部'})
        }
      })
    },
    // 民族  /system/dict/field/ethnicity
    ethnicity(){
       this.$get('/system/dict/field/ethnicity').then(res=>{
         if(res.status == 1){
            this.options7 = res.data
         }
      })
    },
    //  新增学生   /basic/student/add
    addstudent(){
      let form1 = {
       birthday :this.form.birthday,
       code :this.form.code,
       email :this.form.email,
       grade :Number(this.form.grade),
       idNumber :this.form.idNumber,
       name :this.form.name,
       phone :this.form.phone,
       academyId :this.value,
       specialtyId :this.value1,
       classId :this.value3,
       gender :this.value4,
       ethnicity :this.value7,
       atSchoolStatus :this.value5,
       city : this.city,
       province :this.province,
       area :this.area,
      }
      this.$post('/basic/student/add',form1).then(res=>{
          if(res.status == 1){
            this.$notify({
                message: '新增成功',
                type: 'success'
            });
             this.tableData.unshift(res.data)
            this.studentlist()
             this.allxue1()
          }
      })
    },
    handleEdit(index, row) {
      //编辑对话框显示值
      this.addst = '编辑学生'
      this.falg = false
      this.falg1 = true
      this.dialogFormVisible = true
      this.$get(`/basic/academy/all?type=${1}`).then(res=>{
        if(res.status == 1){
          this.options = res.data
        }
      })
      this.$get(`/basic/specialty/all/${row.academyId}`).then(res=>{
        if(res.status == 1){
          this.options1 = res.data
        }
      })
      this.$get(`/basic/class/all/${row.specialtyId}`).then(res=>{
        if(res.status == 1){
          this.options3 = res.data
        }
      })
      this.value = row.academyId
      this.value1 = row.specialtyId
      this.value2 = row.grade
      this.value3 = row.classId
      // this.value6 = row.versionId
      this.value4 = row.gender
      this.value7 = row.ethnicity
      this.value5 = row.atSchoolStatus
      this.province = row.province
      this.city = row.city
      this.area = row.area
      this.form = row
      this.form.id = row.id
      this.allxue()
    },
    seccons4(Form){
      // 确定按钮的点击提交编辑内容
       this.$refs[Form].validate((valid) => {
          if (valid) {
            this.addstudent()
            this.dialogFormVisible = false
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    resetForm(form) {
        this.$refs[form].resetFields();
        this.dialogFormVisible = false
    },
    handleClose(){
       this.$refs['ruleForm'].resetFields();
      this.dialogFormVisible = false
      // this.form = {}
    },
    seccons44(){
      // 确定按钮的点击提交编辑内容   /basic/student/edit
      let form2 = {
          birthday :this.form.birthday,
          code :this.form.code,
          email :this.form.email,
          grade :Number(this.form.grade),
          idNumber :this.form.idNumber,
          name :this.form.name,
          phone :this.form.phone,
          academyId :this.value,
          specialtyId :this.value1,
          classId :this.value3,
          versionId :this.value6,
          gender :this.value4,
          ethnicity :this.value7,
          atSchoolStatus :this.value5,
          city : this.city,
          province :this.province,
          area :this.area,
          id:this.form.id
      }
      this.$put('/basic/student/edit',form2).then(res=>{
        if(res.status == 1){
          this.form2 = res.data
          this.studentlist()
          this.allxue1()
          this.dialogFormVisible = false
        }else{
           this.$message('编辑失败')
           this.dialogFormVisible = true
        }
      })
     
    },
    close(){
      this.addst = '添加学生'
      this.falg = true
      this.falg1 = false
      this.dialogFormVisible = true
      this.value = ''
      this.value1 = ''
      this.value2 = ''
      this.value3 = ''
      this.value4 = ''
      this.value5 = ''
      this.value6 = ''
      this.form = {}
      this.allxue1()
    },
    handleDelete(index, row) {
     // 删除学生信息   /basic/class/del/{id}
      this.$confirm('确定删除该信息?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
      }).then(() => {
             this.$deletes(`/basic/student/del/${row.id}`).then(res=>{
              if(res.status == 1){
                this.onSubmit()
              }
            })
        }).catch(() => {
            this.$message({
                type: 'info',
                message: '已取消删除'
            });
        });
    },
    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.studentlist()
    },
    handleCurrentChange(val) {
     this.pageNum = val;
      this.studentlist();
    }
  },
  mounted(){
    this.allxue()
    this.atSchoolStatus()
    this.onSubmit()
    this.getNodes()
    this.ethnicity()
    this.classgrades()
    // this.changes3()
    this.versionall()
    this.studentlist()
  },
  
};
</script>
